<!-- 车辆管理主页 -->
<template>
	<div class="content">
		<div class="top">
			<span class="name-title">姓名：</span>
			<el-input v-model="userName" class="input marginL30" size="small"></el-input>
			<span class="name-title">手机号码：</span>
			<el-input v-model="tel" class="input" size="small"></el-input>
			<el-button class="button" size="small">查询</el-button>
			<el-button class="button" size="small">增加</el-button>
			<!-- <el-button class="button" size="small">导入</el-button>
			<el-button class="button" size="small">自定义列表</el-button> -->
		</div>
		<el-aside width="100%" style="flex:1;padding: 30px 0 0;">
			<el-table @select="select" size="mini" class="center" style="text-align:center;" ref="singleTable" highlight-current-row :data="tableData" border>
				<!-- <el-table-column width="100" height="40px" type="index" label="序号"></el-table-column> -->
				<el-table-column width="100"  type="selection" height="40px"></el-table-column>
				<el-table-column style="height:48px;" label="操作" width="200">
					<template slot-scope="scope">
						<el-tooltip :enterable="false" class="item" effect="dark" content="编辑" placement="bottom">
							<span class="el-icon-edit-outline el-icon" @click="handleClick(scope.row)"></span>
						</el-tooltip>
						<el-tooltip :enterable="false"s class="item" effect="dark" content="删除" placement="bottom">
							<span class="el-icon-delete el-icon" @click="removeClick(scope.row)"></span>
						</el-tooltip>
					</template>
				</el-table-column>
				<el-table-column prop="userName" width="100" label="姓名"></el-table-column>
				<el-table-column prop="sex" width="100" label="性别"></el-table-column>
				<el-table-column prop="tel" width="200" label="手机号码"></el-table-column>
				<el-table-column prop="userId" min-width="200" label="身份证号"></el-table-column>
				<el-table-column prop="land" width="200" label="籍贯"></el-table-column>
				<el-table-column prop="carType" width="100" label="准驾车型"></el-table-column>
				<el-table-column prop="startDate" width="200" label="驾驶证初次领证日期"></el-table-column>
				<el-table-column prop="endDate" width="200" label="驾驶证有效期"></el-table-column>
			</el-table>
			<div v-if="totalPage" class="block">
				<el-pagination @current-change="changeCurPage" layout="prev, pager, next" :total="totalPage"></el-pagination>
			</div>
		</el-aside>
		<el-dialog :append-to-body="true" width="500px" :title="title" :visible.sync="dialogTableVisible">
			<el-aside style="max-height:300px;width:100%;">
				<el-form :model="formList"  :rules="rules" ref="ruleForm" >
					<el-form-item label="姓名" prop="node" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="formList.userName" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="性别" prop="nodeName" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="formList.sex" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="手机号码" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="formList.tel" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="身份证号" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="formList.userId" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="籍贯" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="formList.land" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="准驾车型" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="formList.carType" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="驾驶证初次领证日期" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="formList.startDate" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="驾驶证有效期" :label-width="formLabelWidth">
						<el-input style="width:90%;" size="small" v-model="formList.endDate" auto-complete="off"></el-input>
					</el-form-item>
				</el-form>
			</el-aside>	
			<div slot="footer" class="dialog-footer">
				<el-button size="small" @click="addCancel">取 消</el-button>
				<el-button size="small" type="primary" @click="addConfirm('ruleForm')">确 定</el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
export default {
	name: 'driverManag',
	data() {
		return {
			totalPage: 0,
			userName: '',
			tel: '',
			tableData: [
			{
				id:'1',
				userName: '王小虎',
				userId:'213169199302130895',
				sex: '男',
				land: '深圳市东方物流',
				carType: 'C1',
				tel: '13568169626',
				startDate: '2015-05-18',
				endDate: '2021-05-18'
			},{
				id:'1',
				userName: '王小虎',
				userId:'213169199302130895',
				sex: '男',
				land: '深圳市东方物流',
				carType: 'C1',
				tel: '13568169626',
				startDate: '2015-05-18',
				endDate: '2021-05-18'
			},
			{
				id:'1',
				userName: '王小虎',
				userId:'213169199302130895',
				sex: '男',
				land: '深圳市东方物流',
				carType: 'C1',
				tel: '13568169626',
				startDate: '2015-05-18',
				endDate: '2021-05-18'
			}
			],
			dialogTableVisible: false,
			title: '',
			rules: {},
			formList: {},
			formLabelWidth: '140px'
		}
	},
	created() {
		this.totalPage = this.tableData.length;
		for(var i = 0;i < this.tableData.length;i++){
			this.tableData[i].checked = false;
		}
	},
	mounted() {

	},
	methods: {
		changeCurPage() {

		},
		//编辑
		handleClick(row){
			console.log(row)
			this.dialogTableVisible = true;
		},
		//删除
		removeClick(row){

		},
		//取消
		addCancel(){

		},
		//确认
		addConfirm(){

		},
		//删除选中的数据
		removeAllListLi() {
			var boole = false;
			for(var i = 0; i < this.tableData.length;i++) {
				if(this.tableData[i].checked) {
					boole = true;
				}
			}
			if(!boole) {
				//证明没有一项选中
				this.$message({
					message: '请至少选中一项才能删除！',
					type: 'warning'
				});
			} else {
				this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning',
					center: true
				}).then(() => {
					for(var i = 0; i < this.tableData.length;i++) {
						if(this.tableData[i].checked) {
							this.tableData.splice(i,1);
							i--;
						}
					}
					this.$message({
						type: 'success',
						message: '删除成功!'
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消删除'
					});
				});
			}
		},
		//选择触发
		select(select,row) {
			var id = row.id;
			for(var i in this.tableData) {
				if(id == this.tableData[i].id) {
					this.tableData[i].checked = !this.tableData[i].checked;
					break;
				}
			}
		},
		//全部选择触发
		selectAll(select) {
			if(select.length > 0) {
				this.selectYes = true;
				for(var i in this.tableData) {
					this.tableData[i].checked = true;
				}
			} else {
				this.selectYes = false;
				for(var i in this.tableData) {
					this.tableData[i].checked = false;
				}
			}
		},
		changeFun(val){

		},
		//分页页码改变
		changeCurPage() {

		}
	}
}
</script>

<style scoped>
.content{flex: 1 !important;padding: 30px 0 0 40px;display: flex;flex-direction: column;overflow: hidden;}
.top{overflow: hidden;}
.top .name-title{float: left;line-height: 32px;}
.top .input{float: left;width: 160px;}
.top .marginL30{margin-right: 30px;}
.top .button{float: left;margin: 0 0 0 20px;}
.block{display: flex;justify-content: center;padding: 20px 0 0;}
.el-icon{font-size: 20px;color: #00a8d4;cursor: pointer;display: inline-block;margin: 0 15px;}
</style>


